import {isString} from 'lodash';

import mainConfig from 'mainConfig';
import requester from 'Services/requester/requester';
import queryParamUpdater from 'Map/utils/queryParamUpdater';
import sessionId from '../configurer/sessionId';
import styleCopy from '../style/styleCopy';

const TOMTOM_ORIGIN = 'api.tomtom.com';

class TierStyleRetriever {
    constructor() {
        this._cache = {};
    }

    _getOrigin = () => mainConfig.origin;

    _transformResponse = (response) => {
        if (this._getOrigin() === TOMTOM_ORIGIN) {
            const newStyle = JSON.parse(response);
            if (newStyle.sprite) {
                newStyle.sprite = queryParamUpdater(newStyle.sprite, 'sessionId', sessionId.getInstance().get());
            }
            if (newStyle.glyphs) {
                newStyle.glyphs = queryParamUpdater(newStyle.glyphs, 'sessionId', sessionId.getInstance().get());
            }

            return newStyle;
        }

        return JSON.parse(response.replace(new RegExp(TOMTOM_ORIGIN, 'g'), this._getOrigin()));
    }

    _download = (styleUrl) => {
        const options = {url: styleUrl};
        return requester.get(options, this._transformResponse);
    };

    fetch = async (tier) => {
        const styleUrl = tier.getStyle();
        if (!isString(styleUrl)) {
            tier.setStyle(styleUrl);
            return tier;
        }

        let style = this._cache[styleUrl];
        if (!style) {
            style = await this._download(styleUrl);
            this._cache[styleUrl] = style;
        }

        tier.setStyle(styleCopy(style));
        return tier;
    };
}

export default () => {
    return new TierStyleRetriever();
};



